.card-bottom{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.details {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .quantity span {
    font-size: 1rem;
    font-weight: bold;
  }

  .actions {
    display: flex;
    justify-content: flex-end;
    /* margin: 0.2rem 0; */
  }
  
  .actions button {
    background-color: transparent;
    border: 1px solid white;
    margin-left: 0.5rem;
    padding: 0.15rem 1rem;
    background-color: white;
    color: #4b4b4b;
    border: 1px solid #4b4b4b;
    border-radius: 2px;
  }
  
  .actions button:hover,
  .actions button:active {
    color: white;
    background-color: #4b4b4b;
    border-radius: 2px;
  }