.header {
    position: fixed;
    top: 0;
    /* left: 0; */
    width: 80%;
    height: 5.5rem;
    background-color: #e3e1e1;
    color: #1f1f20;
    /* color: #3c2f2f; */
    /* color: 614124; */
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
}

.logo-title{
    display: flex;
    flex-direction: row;
    /* flex-wrap: wrap; */
    justify-content:flex-start;
    /* border: 2px solid black; */
    width: 50%;
}

.logo-div{
    height: 4.5rem;
    width: 4.5rem;
    margin-right: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo-div img{
    /* border: 1px black solid; */
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
    height: 4rem;
    width: 4rem;
}

/* .box-in-header{
